.recipe-container{
    display:block;
    color:white;
    z-index: -1;
    margin:0em  0em 2em 0em;
    &__wrapper{    
        margin: 0 auto;
        max-width: 100%;
        display:grid;
        gap:3px 2px;
        grid-template-rows: auto;
        @include respond-to(mobile-portrait){
            grid-template-columns: 1fr;    
        }
        @include respond-to(mobile-landscape){
            grid-template-columns: 1fr 1fr;
        }
        @include respond-to(tablet-portrait){
            grid-template-columns: 1fr 1fr 1fr;
        }
        @include respond-to(tablet-landscape){
            grid-template-columns: 1fr 1fr 1fr;
        }
        @include respond-to(large-screen){
            grid-template-columns: 1fr 1fr 1fr 1fr;
        }
        @include respond-to(larger-screen){
            grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
        }
        // @include respond-to(medium-landscape-568){
        //     // width:90%; 
        //     // width:80%; 
        //     width:50%; 
        // }        
        a{
            h1{
                font-size:1.2rem;
            }
        }
    }
    &__item{
        border-radius: 15px;
        margin:.4em auto;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        width:90%;
        padding:3em .5em;
        display: flex;
        flex-direction: column;
        box-shadow:0 .5rem 1rem rgba($color-black, .2);
        font-size:1rem;
        transition: all .7s ease;
    }
    &__item:hover{
        transition: all .5s;
        transform: translateY(5px);
        box-shadow:0 .5rem 10rem rgba($color-black, .1);
        
    }
    &__title{
        color:$color-white;
        font-weight:500;
        margin-bottom:1em;
        @include respond-to(mobile-portrait){
            font-size: 1.5rem;
        }
        @include respond-to(mobile-landscape){
            font-size: 1.4rem;
        }
        @include respond-to(tablet-portrait){
            font-size: 1.3rem;
        }
        @include respond-to(large-screen){
            font-size:1.3rem;
        }
        
        
    }
    &__type{
        
        &--title{
            // font-size:1rem;
        }
        &--value{
            color:$color-white;
            min-width: max-content;
        }
    }
    &__calories{
        &--title{
            font-size: 1em;
        }
        &--value{
            color:$color-white;
            min-width: max-content;
            font-size: 1rem;
            // margin-left:1em;
        }
    }
    &__carbs{
        &--title{
            // font-size: 1em;
        }
        &--value{
            color:$color-white;
            min-width: max-content;
            font-size: 1rem;
            // margin-left:1em;
        }
    }
    &__type,&__calories,&__carbs{
        display:flex;
        min-width: 100%;
        justify-content: space-between;
        font-weight:400;
        color:$color-primary;
        transition: all 2s ease;
        &--title{
            margin-left:-.2em;
        }
        &--value{
            // font-size:.7em;
        }
        @include respond-to(mobile-portrait){
            font-size:1rem;
        }
        @include respond-to(mobile-landscape){
            font-size:1rem;
        }
        @include respond-to(tablet-portrait){
            font-size:1rem;
        }
        @include respond-to(tablet-landscape){
            font-size:1rem;
        }
        @include respond-to(large-screen){
            font-size:1rem;
            
        }
        @include respond-to(larger-screen){
            font-size:1rem;
            
        }
    }
    // p{
    //     color:$color-primary;
    // }
    img{
        max-width:100%
    }
    
    // h3{
    //     color:$color-primary-1;
    //     text-align: center;
    // }
    
}